/* General Reset */
body {
    margin: 0;
    font-family: 'Poppins', sans-serif;
    background-color: #f4f5f7;
    color: #444;
  }
  
  /* Container */
  .friend-profile-container {
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
    padding: 2rem;
    background: linear-gradient(135deg, #eceff1, #cfd8dc);
  }
  
  /* Card */
  .friend-profile-card {
    background-color: #fff;
    border-radius: 15px;
    box-shadow: 0 8px 20px rgba(0, 0, 0, 0.1);
    max-width: 450px;
    width: 100%;
    overflow: hidden;
    transition: transform 0.3s ease, box-shadow 0.3s ease;
  }
  
  .friend-profile-card:hover {
    transform: translateY(-10px);
    box-shadow: 0 12px 30px rgba(0, 0, 0, 0.15);
  }
  
  /* Header */
  .friend-profile-header {
    background: linear-gradient(135deg, #6a11cb, #2575fc);
    color: #fff;
    padding: 2rem 1.5rem;
    text-align: center;
  }
  
  .friend-profile-name {
    font-size: 2rem;
    font-weight: 700;
  }
  
  .friend-profile-username {
    font-size: 1.1rem;
    margin-top: 0.5rem;
    opacity: 0.9;
  }
  
  /* Details Section */
  .friend-profile-details {
    padding: 1.5rem;
    font-size: 1rem;
    line-height: 1.8;
    color: #555;
  }
  
  .friend-profile-details p {
    margin: 0.8rem 0;
  }
  
  /* Buttons Section */
  .friend-profile-buttons {
    display: flex;
    justify-content: center;
    padding: 1rem;
  }
  
  .friend-profile-back-btn {
    display: inline-block;
    padding: 0.8rem 1.5rem;
    margin-right: 1rem;
    background: #2575fc;
    color: #fff;
    font-weight: 600;
    font-size: 1rem;
    border-radius: 10px;
    text-decoration: none;
    box-shadow: 0 4px 12px rgba(37, 117, 252, 0.4);
    transition: background 0.3s ease, transform 0.2s ease;
  }
  
  .friend-profile-back-btn:hover {
    background: #6a11cb;
    transform: translateY(-3px);
  }
  
  /* Chat Section */
  .friend-profile-chat-section {
    padding: 1.5rem;
    background-color: #f9fafb;
    border-top: 1px solid #e0e0e0;
    text-align: center;
  }
  
  .friend-profile-chat-section button {
    display: inline-block;
    padding: 0.8rem 1.5rem;
    font-size: 1rem;
    font-weight: 600;
    color: #fff;
    background: #6a11cb;
    border: none;
    border-radius: 8px;
    cursor: pointer;
    box-shadow: 0 4px 12px rgba(106, 17, 203, 0.4);
    transition: background 0.3s ease, transform 0.2s ease;
  }
  
  .friend-profile-chat-section button:hover {
    background: #2575fc;
    transform: translateY(-3px);
  }
  
  /* Responsive Design */
  @media (max-width: 600px) {
    .friend-profile-card {
      max-width: 100%;
      padding: 1rem;
    }
  
    .friend-profile-name {
      font-size: 1.6rem;
    }
  
    .friend-profile-username {
      font-size: 1rem;
    }
  
    .friend-profile-back-btn {
      margin-right: 0;
      width: 100%;
      text-align: center;
    }
  }
  