/* General Reset */
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  }
  
  body {
    background-color: #f9fafb;
    color: #333;
    line-height: 1.6;
  }
  
  /* Loading Spinner */
  .loading-spinner {
    text-align: center;
    font-size: 1.2rem;
    padding: 20px;
  }
  
  /* Container */
  .profile-container {
    max-width: 800px;
    margin: 50px auto;
    background: #ffffff;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    border-radius: 8px;
    overflow: hidden;
    animation: fadeIn 0.8s ease-in-out;
  }
  
  @keyframes fadeIn {
    from {
      opacity: 0;
    }
    to {
      opacity: 1;
    }
  }
  
  /* Tabs */
  .tabs {
    display: flex;
    background-color: #f0f4f8;
  }
  
  .tab-button {
    flex: 1;
    padding: 12px;
    font-size: 1rem;
    border: none;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 8px;
    transition: background-color 0.3s;
  }
  
  .tab-button.active {
    background-color: #007bff;
    color: white;
    font-weight: bold;
  }
  
  .tab-button:hover {
    background-color: #e1e5ea;
  }
  
  .tab-icon {
    font-size: 1.2rem;
  }
  
  /* Content */
  .content {
    padding: 20px;
  }
  
  .profile-section,
  .friends-section {
    text-align: center;
  }
  
  .section-icon {
    font-size: 3rem;
    color: #007bff;
    margin-bottom: 10px;
  }
  
  .user-details p {
    margin: 8px 0;
    font-size: 1rem;
    color: #555;
  }
  
  .verified {
    margin-top: 15px;
    color: #28a745;
    font-weight: bold;
  }
  
  .not-verified {
    margin-top: 15px;
    color: #dc3545;
    font-weight: bold;
  }
  
  .info-text {
    margin-top: 10px;
    color: #007bff;
    font-size: 0.9rem;
  }
  
  .friend-list {
    display: flex;
    flex-direction: column;
    gap: 10px;
    margin-top: 20px;
  }
  
  .friend-list div {
    padding: 12px;
    background: #f0f4f8;
    border-radius: 6px;
    transition: transform 0.2s ease;
  }
  
  .friend-list div:hover {
    transform: scale(1.02);
    background-color: #e1e5ea;
  }
  
  /* Buttons */
  .button {
    display: inline-block;
    margin-top: 15px;
    padding: 10px 15px;
    font-size: 1rem;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: transform 0.2s ease;
  }
  
  .button:hover {
    transform: scale(1.05);
  }
  
  .resend-button {
    background-color: #28a745;
  }
  
  .signout-button {
    background-color: #dc3545;
  }
  
  .resend-button:hover {
    background-color: #218838;
  }
  
  .signout-button:hover {
    background-color: #c82333;
  }
  
  /* Responsive */
  @media (max-width: 600px) {
    .tab-button {
      font-size: 0.9rem;
      padding: 8px;
    }
  
    .section-icon {
      font-size: 2rem;
    }
  
    .user-details p,
    .friend-list div {
      font-size: 0.9rem;
    }
  
    .button {
      padding: 8px 12px;
    }
  }
  